import { Menu } from 'antd'
import { useState, useEffect } from 'react'
import { useLocation, useNavigate } from 'react-router'
import { NavContainer } from './style'

const { SubMenu } = Menu

const Nav = ({ style }) => {
  const navigate = useNavigate()
  const [current, setCurrent] = useState('app')
  const { state } = useLocation()

  // state
  useEffect(() => {
    setCurrent(state?.current ?? 'app')
  }, [state])

  // 
  const handleClick = (e) => {
    setCurrent(e.key)
    console.log('2!!!!', e.key)
    navigate(`/`, { state: { current: e.key } })
  }

  return (
    <NavContainer style={style}>
      <Menu
        className="layout-menu"
        onClick={handleClick}
        selectedKeys={[current]}
        mode="horizontal"
      >
        <Menu.Item key="app"></Menu.Item>
        <Menu.Item key="recommend"></Menu.Item>
        <Menu.Item key="news_society"></Menu.Item>
        <Menu.Item key="news_entertainment"></Menu.Item>
        <Menu.Item key="news_tech"></Menu.Item>
        <Menu.Item key="news_military"></Menu.Item>
        <Menu.Item key="news_sports"></Menu.Item>
        <Menu.Item key="news_car"></Menu.Item>
        <SubMenu key="SubMenu" title="">
          <Menu.Item key="news_finance"></Menu.Item>
          <Menu.Item key="news_world"></Menu.Item>
          <Menu.Item key="news_fashion"></Menu.Item>
          {/* <Menu.Item key="news_travel"></Menu.Item> */}
          {/* <Menu.Item key="news_discovery"></Menu.Item> */}
          {/* <Menu.Item key="news_baby"></Menu.Item> */}
          {/* <Menu.Item key="news_regimen"></Menu.Item> */}
          {/* <Menu.Item key="news_story"></Menu.Item> */}
          {/* <Menu.Item key="news_essay"></Menu.Item> */}
          {/* <Menu.Item key="news_game"></Menu.Item> */}
          <Menu.Item key="news_history"></Menu.Item>
          {/* <Menu.Item key="news_food"></Menu.Item> */}
          <Menu.Item key="news_legal"></Menu.Item>
          <Menu.Item key="news_politics"></Menu.Item>
          <Menu.Item key="news_air"></Menu.Item>
        </SubMenu>
      </Menu>
    </NavContainer>
  )
}

export default Nav
