let cancel

export const articleSearch = (params) => {
  if (cancel) cancel()
  const source = CancelToken.source()
  cancel = source.cancel
  return instance({
    url: '/article_search',
    method: 'GET',
    params,
    cancelToken: source.token,
  })
}
